import { useState, useEffect } from 'react';
import { Link as RouterLink } from 'react-router-dom';
// material
import { styled } from '@material-ui/core/styles';
import { useFormik, Form, FormikProvider } from 'formik';

// layouts
import AuthLayout from '../layouts/AuthLayout';
import * as Yup from 'yup';

import {
    Card,
    Link,
    Stack,
    Avatar,
    Button,
    Checkbox,
    TableRow,
    TableBody,
    TableCell,
    Container,
    Typography,
    TableContainer,
    TablePagination,
    TextField,
    IconButton,
    InputAdornment,
    Box
  } from '@material-ui/core';
import { LoadingButton } from '@material-ui/lab';

// components
import Page from '../components/Page';
import { MHidden } from '../components/@material-extend';
import { useHistory, useLocation, useParams } from 'react-router';
import CustomizedSnackbars, { useAlertData } from './component/alert/CustomizedSnackbars'
import HOST from '../components/Env';


// ----------------------------------------------------------------------

const RootStyle = styled(Page)(({ theme }) => ({
  [theme.breakpoints.up('md')]: {
    display: 'flex'
  }
}));

const SectionStyle = styled(Card)(({ theme }) => ({
  width: '100%',
  maxWidth: 464,
  display: 'flex',
  flexDirection: 'column',
  justifyContent: 'center',
  margin: theme.spacing(2, 0, 2, 2)
}));

const ContentStyle = styled('div')(({ theme }) => ({
  maxWidth: 480,
  margin: 'auto',
  display: 'flex',
  minHeight: '100vh',
  flexDirection: 'column',
  justifyContent: 'center',
  padding: theme.spacing(12, 0)
}));

// ----------------------------------------------------------------------

function EmailForm() {
   
    
    const [ alertToggleData, setAlertToggelData ] = useState({
      isOpen: false,
      message: '',
      type: 'success'
    });
  
  
    const insertApi = val => {
      fetch(`${HOST}user/forgot`, {
        method: 'POST',
        body:JSON.stringify(val),
        headers: {
          'Content-Type': 'application/json'
       },
      })
        .then(respose => respose.json())
        .then(doc => {
          console.log(doc)
          handleReset();
          if(doc.err){
             setAlertToggelData((pre)=>({...pre,type:'warning', message:doc.message, isOpen:true, toggle: !alertToggleData.toggle}));
          }else{
             setAlertToggelData((pre)=>({...pre,type:'success', message:doc.message, isOpen:true, toggle: !alertToggleData.toggle}));
          }
        })
        .catch(err => console.log(err))
    }
  
    const RegisterSchema = Yup.object().shape({
      email: Yup.string().email('Email must be a valid email address')
        .min(2, 'Too Short!')
        .max(50, 'Too Long!')
        .required('Current Password Required'),
    });
  
    
    let formik = useFormik({
    
      initialValues: {
        email:'',
      },
      validationSchema: RegisterSchema,
      onSubmit: (value) => {
        const val = value;
        insertApi(val);
      }
  });
  
  const { errors, touched, handleSubmit, isSubmitting, getFieldProps, handleReset } = formik;
  
  return (
    <FormikProvider value={formik}>
  
      <Form autoComplete="off" noValidate onSubmit={handleSubmit}>
  
        <Stack spacing={3}>
          <TextField
            fullWidth
            label={"Email" }
            {...getFieldProps('email')}
            error={Boolean(touched.email && errors.email)}
            helperText={touched.email && errors.email}
            placeHolder='Category Description'
          />
          <LoadingButton
            fullWidth
            size="large"
            type="submit"
            variant="contained"
            loading={isSubmitting}
          >
            submit
          </LoadingButton>
        </Stack>
      </Form>
      <CustomizedSnackbars isOpen={alertToggleData.isOpen} message={alertToggleData.message} toggle={alertToggleData.toggle} type={alertToggleData.type} />
  
    </FormikProvider>
  );
  }




function AddForm({tokenId}) {
    const history = useHistory(); 
    const [success, setSuccess] = useState(false);
    const [ alertToggleData, setAlertToggelData ] = useState({
      isOpen: false,
      message: '',
      type: 'success'
    });
  
  
    const insertApi = val => {
      fetch(`${HOST}user/forgot`, {
        method: 'PUT',
        body:JSON.stringify({password: val.password, user_id: tokenId}),
        headers: {
          'Content-Type': 'application/json'
       },

      })
        .then(respose => respose.json())
        .then(doc => {
          console.log(doc);
          handleReset();
          if(doc.err){
             setAlertToggelData((pre)=>({...pre,type:'warning', message:doc.message, isOpen:true, toggle: !alertToggleData.toggle}));
          }else{
             setAlertToggelData((pre)=>({...pre,type:'success', message:doc.message, isOpen:true, toggle: !alertToggleData.toggle}));
             setSuccess(true);
          }
        })
        .catch(err => console.log(err));
    }
    const RegisterSchema = Yup.object().shape({
      password: Yup.string().min(2, 'Too Short!').required('New Password Required'),
      confirm_password: Yup.string().oneOf([Yup.ref('password'), null], 'Passwords must match').required('Confirm Password is required')
      // email: Yup.string().email('Email must be a valid email address').required('Email is required'),
      // password: Yup.string().required('Password is required')
    });
  
    
    let formik = useFormik({
    
      initialValues: {
        password:'',
        confirm_password: '', 
      },
      validationSchema: RegisterSchema,
      onSubmit: (value) => {
        const val = value;
        insertApi(val);
      }
  });
  
   
  
  const { errors, touched, handleSubmit, isSubmitting, getFieldProps, handleReset } = formik;
  
  return (
    <FormikProvider value={formik}>
  
      {success ?<><Typography variant="h3" sx={{ px: 5, mt: 10, mb: 5 }}>
            Your Password Has Been Changed Successfull
            
          </Typography>
          <Typography variant="h5" sx={{ px: 5, mt: 10, mb: 5, mx: 'auto' }}>

          <Link component={RouterLink} variant="subtitle2" to="/login">
          Click Here For Login
          </Link>

          </Typography>
           
          
        </>
           : 
           <Form autoComplete="off" noValidate onSubmit={handleSubmit}>
  
        <Stack spacing={3}>
          <TextField
            fullWidth
            label={"New Password" }
            {...getFieldProps('password')}
            error={Boolean(touched.password && errors.password)}
            helperText={touched.password && errors.password}
            placeHolder='Category Description'
          />
           <TextField
            fullWidth
            label={"Confirm Password"}
            {...getFieldProps('confirm_password')}
            error={Boolean(touched.confirm_password && errors.confirm_password)}
            helperText={touched.confirm_password && errors.confirm_password}
            placeHolder={'Confirm Password'}          
          />
          <LoadingButton
            fullWidth
            size="large"
            type="submit"
            variant="contained"
            loading={isSubmitting}
          >
            Update
          </LoadingButton>
        </Stack>
      </Form> }
      <CustomizedSnackbars isOpen={alertToggleData.isOpen} message={alertToggleData.message} toggle={alertToggleData.toggle} type={alertToggleData.type} />
  
    </FormikProvider>
  );
  }
  


export default function ForgotPassword() {
    const { token } = useParams();
    const [tokenId, setTokenId] = useState(null);

    console.log(token);
    if(token){
      useEffect(() => {
        fetch(`${HOST}user/forgot`,{
            method: "GET",
            headers:{
                'Authorization': `bearer ${token}`
            }
        })
        .then(doc=>doc.json())
        .then(doc=>{
            console.log(doc);
            setTokenId(doc.token);
        })
        .catch((err)=>console.log(err));
    }, []);
    }
    
  return (
    <RootStyle title="Register | Minimal-UI">
      <AuthLayout>
        Already have an account? &nbsp;
        <Link underline="none" variant="subtitle2" component={RouterLink} to="/login">
          Login
        </Link>
      </AuthLayout>

      <MHidden width="mdDown">
        <SectionStyle>
          <Typography variant="h3" sx={{ px: 5, mt: 10, mb: 5 }}>
            Forget Password
          </Typography>
          <img alt="register" src="/static/illustrations/illustration_register.png" />
        </SectionStyle>
      </MHidden>

      <Container>
        <ContentStyle>
          <Box sx={{ mb: 5 }}>
            <Typography variant="h4" gutterBottom>
               Forget Password
            </Typography>
            <Typography sx={{ color: 'text.secondary' }}>
            Enter your email
            </Typography>
          </Box>
          {tokenId ? <AddForm tokenId={tokenId} /> : <EmailForm />}
          <MHidden width="smUp">
            <Typography variant="subtitle2" sx={{ mt: 3, textAlign: 'center' }}>
              Already have an account?&nbsp;
              <Link to="/login" component={RouterLink}>
                Login
              </Link>
            </Typography>
          </MHidden>
        </ContentStyle>
      </Container>
    </RootStyle>
  );
}
