const RootStyle = styled(Page)(({ theme }) => ({
  [theme.breakpoints.up('md')]: {
    display: 'flex'
  }
}));

const SectionStyle = styled(Card)(({ theme }) => ({
  width: '100%',
  maxWidth: 464,
  display: 'flex',
  flexDirection: 'column',
  justifyContent: 'center',
  margin: theme.spacing(2, 0, 2, 2)
}));

const ContentStyle = styled('div')(({ theme }) => ({
  minWidth: '100%',
  margin: 'auto ',
  display: 'flex',
 
  flexDirection: 'column',
  justifyContent: 'center',
  padding: theme.spacing(4, 0)
}));


let idCah = false;
function AddPostForm({ initValue }) {
  let { id } = useParams();
  let history = useHistory();

  console.log(id);

  
  const [ alertToggleData, setAlertToggelData ] = useState({
    isOpen: false,
    message: '',
    type: 'success'
  });

  console.log(initValue);

  const insertApi = val => {
      console.log(val)
    fetch(`${HOST}category/add`, {
      method: 'POST',
      body:JSON.stringify(val),
      headers: {
        'Content-Type': 'application/json',
     },
    })
      .then(respose => respose.json())
      .then(doc => {
        console.log(doc)
        handleReset();
      setAlertToggelData((pre)=>({...pre, message:doc.message, isOpen:true, toggle: !alertToggleData.toggle}));

      })
      .catch(err => console.log(err))
  }

  const editApi = val => {
    console.log(val)
  fetch(`${HOST}category/upd`, {
    method: 'PUT',
    body:JSON.stringify(val),
    headers: {
      'Content-Type': 'application/json',
   },
  })
    .then(respose => respose.json())
    .then(doc => {
      console.log(doc)
      handleReset();
      setAlertToggelData((pre)=>({...pre, message:doc.message, isOpen:true, toggle: !alertToggleData.toggle}));
      history.push('/admin/category');
    })
    .catch(err => console.log(err))
  }
  const RegisterSchema = Yup.object().shape({
    category_title: Yup.string()
      .min(2, 'Too Short!')
      .max(50, 'Too Long!')
      .required('Title Is Required'),
    category_description: Yup.string().min(2, 'Too Short!')
    // email: Yup.string().email('Email must be a valid email address').required('Email is required'),
    // password: Yup.string().required('Password is required')
  });

  
  let formik = useFormik({
  
    initialValues: {
      category_title: initValue.categoryTitle ?? '',
      category_description: initValue.description ?? '', 
    },
    validationSchema: RegisterSchema,
    onSubmit: (value) => {
      const val = value;
      if(id !== undefined){
        editApi({
            // title: val.category_title === '' ? initValue.categoryTitle : val.category_title,
            title: val.category_title,
            description: val.category_description,
            userId: id
         })
      }else{
      insertApi({
          title: val.category_title,
          description: val.category_description
      })
      }
    }
  });

 

const { errors, touched, handleSubmit, isSubmitting, getFieldProps, handleReset } = formik;

return (
  <FormikProvider value={formik}>

    <Form autoComplete="off" noValidate onSubmit={handleSubmit}>

      <Stack spacing={3}>
        {/* <Stack direction={{ xs: 'column', sm: 'row' }} spacing={2}> */}
        <TextField
          fullWidth
          label={"Category Title"}
          {...getFieldProps('category_title')}
          error={Boolean(touched.category_title && errors.category_title)}
          helperText={touched.category_title && errors.category_title}
          placeHolder={'Category Title'}
          
        />

        <TextField
          fullWidth
          multiline
          rows={4}
          label={"Category Description" }
          {...getFieldProps('category_description')}
          error={Boolean(touched.category_description && errors.category_description)}
          helperText={touched.category_description && errors.category_description}
          placeHolder='Category Description'
          
        />


        <LoadingButton
          fullWidth
          size="large"
          type="submit"
          variant="contained"
          loading={isSubmitting}
        >
          {id ? "Update" : "Submit"}
        </LoadingButton>
      </Stack>
    </Form>
    <CustomizedSnackbars isOpen={alertToggleData.isOpen} message={alertToggleData.message} toggle={alertToggleData.toggle} />

  </FormikProvider>
);
}

export default function AddCategory() {
  let { id } = useParams();

  const [change, setChange] = useState(false);
  const [initValue, setInitValue] = useState({
  });

   console.log(initValue);

  if(id !== undefined){
    useEffect(() => {
      
      fetch(`${HOST}category/find/${id}`)
      .then(res=>res.json())
      .then(res=>{
          if(res){
        setInitValue(res);
        console.log(res);
        }
      }).then(
        setTimeout(()=>setChange(true),100)
        // setChange(true)
      )
      .catch(err=>console.log(err))
      
    }, [id])
  }else{
    setTimeout(()=>setChange(true),0);
  }

  return (
    <Page title="User | Minimal-UI">
      <Container>
        <Box pt={12}></Box>
        <Stack direction="row" alignItems="center" justifyContent="space-between" mb={5}>
          <Typography variant="h4" gutterBottom>
            {id ? "Edit Category" : "Add Category"}
          </Typography>
        </Stack>
        <Card >
            <RootStyle>
              <ContentStyle>
                <Container>
                {change && <AddPostForm initValue={initValue} /> }
                </Container>
              </ContentStyle>
            </RootStyle>
        </Card>
      </Container>
    </Page>
  );
}
