import React from 'react'
import { Container, Navbar, Nav, DropdownButton, Dropdown, OverlayTrigger, Tooltip, Button } from 'react-bootstrap';
import Rbutton from '../Rbutton/Rbutton';
import './Navbar.css'
import { Link } from 'react-router-dom';

const NavbarE = () => {
  return (
    <Navbar bg="dark" fixed="top" variant="primary">
    <Container>
      <Navbar.Brand ><Link to='/'><img className="navbar-brand" src='../img/logo.png' /></Link></Navbar.Brand>
      <Nav className="me-auto nav-cus ml-auto" activeKey="/home">

        <Nav.Item className="nav-item">
          <Nav.Link variant="secondary" className="nav-item"><Link to='/'>home</Link></Nav.Link>
        </Nav.Item>
        <Nav.Item >
          <Nav.Link eventKey="link-1" className="nav-item"><Link to='/feature/'>Featurs</Link></Nav.Link>
        </Nav.Item>

        <Nav.Item as={Link} to="/price/" >
          <Nav.Link eventKey="price" className="nav-item"><Link to='/price/'>Price</Link></Nav.Link>
        </Nav.Item>

        <Nav.Item as={Link} >
          <Nav.Link eventKey="link-2" className="nav-item">
            <Dropdown className="d-inline mx-2">
              <Dropdown.Toggle as='a' id="dropdown-autoclose-true">
                Pages
              </Dropdown.Toggle>
              <Dropdown.Menu>
                <Dropdown.Item as={Link} to="/feature/">Featurs</Dropdown.Item>
                <Dropdown.Item as={Link} to="/price/">Price</Dropdown.Item>
                <Dropdown.Item as={Link} to="/element/">Element</Dropdown.Item>
              </Dropdown.Menu>
            </Dropdown>
          </Nav.Link>
        </Nav.Item>

        <Nav.Item >
          <Nav.Link eventKey="link-1" className="nav-item"> <Link to='/blogs/'>
                Blogs
              </Link>
              </Nav.Link>
              
        </Nav.Item>

        <Nav.Item >
          <Nav.Link eventKey="link-2" className="nav-item"><Link to='/contacts/'>Contact</Link></Nav.Link>
        </Nav.Item>

      </Nav>

    </Container>
  </Navbar>
  )
}

export default NavbarE
