import { filter } from 'lodash';
import { Icon } from '@iconify/react';
import { sentenceCase } from 'change-case';
import { useState, useEffect } from 'react';
import plusFill from '@iconify/icons-eva/plus-fill';
import { Link as RouterLink,useHistory } from 'react-router-dom';
// material
import {
  Card,
  Table,
  Stack,
  Avatar,
  Button,
  Checkbox,
  TableRow,
  TableBody,
  TableCell,
  Container,
  Typography,
  TableContainer,
  TablePagination,
  Box
} from '@material-ui/core';
// components
import Page from '../components/Page';
import Label from '../components/Label';
import Scrollbar from '../components/Scrollbar';
import SearchNotFound from '../components/SearchNotFound';
import Confirmation from '../components/Confirmation'
import { UserListHead, UserListToolbar, UserMoreMenu } from '../components/_dashboard/user';
//
import contactList from '../_mocks_/user';
import HOST from '../components/Env'
import CustomizedSnackbars, { useAlertData } from './component/alert/CustomizedSnackbars'



// ----------------------------------------------------------------------

const TABLE_HEAD = [
  { id: 'name', label: 'Name', alignRight: false },
  { id: 'email', label: 'Email', alignRight: false },
  { id: 'message', label: 'Message', alignRight: false },
  { id: 'subject', label: 'Subject', alignRight: false },

];

// ----------------------------------------------------------------------

function descendingComparator(a, b, orderBy) {
  if (b[orderBy] < a[orderBy]) {
    return -1;
  }
  if (b[orderBy] > a[orderBy]) {
    return 1;
  }
  return 0;
}

function getComparator(order, orderBy) {
  return order === 'desc'
    ? (a, b) => descendingComparator(a, b, orderBy)
    : (a, b) => -descendingComparator(a, b, orderBy);
}

function applySortFilter(array, comparator, query) {
  const stabilizedThis = array.map((el, index) => [el, index]);
  stabilizedThis.sort((a, b) => {
    const order = comparator(a[0], b[0]);
    if (order !== 0) return order;
    return a[1] - b[1];
  });
  if (query) {
    return filter(array, (_user) => _user.name.toLowerCase().indexOf(query.toLowerCase()) !== -1);
  }
  return stabilizedThis.map((el) => el[0]);
}

export default function ContactList() {
  const history = useHistory();
  const [page, setPage] = useState(0);
  const [order, setOrder] = useState('asc');
  const [selected, setSelected] = useState([]);
  const [contactList, setContectList] = useState([]);
  const [deleteToggel, setDeleteToggel] = useState(false);
  const [ refresh, setRefresh ] = useState(false);
  const [ currentId, setCurrentId ] = useState('');
  const [orderBy, setOrderBy] = useState('name');
  const [filterName, setFilterName] = useState('');
  const [rowsPerPage, setRowsPerPage] = useState(5);
  const [rowsTotal, setRowsTotal ] = useState(10);
  const [ selectedAll, setSelectedAll] = useState(false);
  const [ alertToggleData, setAlertToggelData ] = useState({
    isOpen: false,
    message: '',
    type: 'success',
    toggle: false
  });



console.log(contactList)
  useEffect(() => {
    fetch(`${HOST}contactus/filter?page=${page}&row=${rowsPerPage}`, {
      method: 'GET',
      headers: {
      'Content-Type': 'application/json',
     }
    }).then(respose=>respose.json())
    .then(doc=>{
      (doc.data).map((ele)=>setContectList((pre)=>[...pre, ele]));
      setRowsTotal(doc.total);
    })
    .catch(err=>console.log(err))
    return ( () => setContectList(()=>[]))
  }, [refresh]);


 const deleteContact = (val) => {
    fetch(`${HOST}contactus/delete`, {
      method: 'DELETE',
      body: JSON.stringify({ id: val }),
      headers: {
      'Content-Type': 'application/json',
     }
    }).then(respose=>respose.json())
    .then(doc=>{
      console.log(doc)
      setSelected([]);
      setRefresh(!refresh);
      setAlertToggelData((pre)=>({...pre, message:doc.message, isOpen:true, toggle: !alertToggleData.toggle}));
    })
    .catch(err=>console.log(err));
  }
  const trashApi = (val) =>{
    fetch(`${HOST}contactus/trash`, {
      method: 'PUT',
      body:JSON.stringify({userId:val,status:'deActive'}),
      headers: {
        'Content-Type': 'application/json',
     },
     }).then(respose=>respose.json())
      .then(doc=>{
        console.log(doc)
        setRefresh(!refresh);
        setSelected([])
        setAlertToggelData((pre)=>({...pre, message:doc.message, isOpen:true, toggle: !alertToggleData.toggle}));
      }).catch((err)=>console.log(err))
  }
  

  const handleRequestSort = (event, property) => {
    const isAsc = orderBy === property && order === 'asc';
    setOrder(isAsc ? 'desc' : 'asc');
    setOrderBy(property);
  };

  const handleSelectAllClick = (event) => {
    if (event.target.checked) {
      const newSelecteds = contactList.map((n) => n._id);
      setSelected(newSelecteds);
      return;
    }
    setSelected([]);
  };

  const handleClick = (event, name) => {
    const selectedIndex = selected.indexOf(name);
    let newSelected = [];
    if (selectedIndex === -1) {
      newSelected = newSelected.concat(selected, name);
    } else if (selectedIndex === 0) {
      newSelected = newSelected.concat(selected.slice(1));
    } else if (selectedIndex === selected.length - 1) {
      newSelected = newSelected.concat(selected.slice(0, -1));
    } else if (selectedIndex > 0) {
      newSelected = newSelected.concat(
        selected.slice(0, selectedIndex),
        selected.slice(selectedIndex + 1)
      );
    }
    setSelected(newSelected);
  };

  const handleChangePage = (event, newPage) => {
    setPage(newPage);
    setRefresh(!refresh);
  };

  const handleChangeRowsPerPage = (event) => {
    setRowsPerPage(parseInt(event.target.value, 10));
    setPage(0);
    setRefresh(!refresh);
  };

  const handleFilterByName = (event) => {
    setFilterName(event.target.value);
  };

  const emptyRows = page > 0 ? Math.max(0, (1 + page) * rowsPerPage - contactList.length) : 0;

  const filteredUsers = applySortFilter(contactList, getComparator(order, orderBy), filterName);

  const isUserNotFound = filteredUsers.length === 0;

  const handleOnClickDeleteConfirm = (value) =>{
    const val = value
     if(val === 'true'){
       selectedAll ? deleteContact(selected) : deleteContact([currentId]);
     }
     setDeleteToggel(false);
     setSelectedAll(false);
  }

  const actionHandler = async (ids, value) =>{
    const id = ids;
    const val = value;
    setCurrentId(id)
    console.log(id,val)
  if(val === 'delete'){
    setDeleteToggel(true);   
  }else if(val === 'trash'){
      // restoreCategory(id)
      console.log('this not the featurn')
  }else if(val === 'edit'){
   history.push({pathname:`/admin/addcontact/${id}`,state:{id},seacrh:{id}}) ; 
}}


const deletaManyHandel = (val) => {
  if(val === "delete"){
    setSelectedAll(true);
    setDeleteToggel(true);
  }else if(val === 'trash'){
    trashApi(selected)
  }
}

  return (
    <Page title="User | Minimal-UI">
      {deleteToggel && <Confirmation handleOnClickDeleteConfirm={handleOnClickDeleteConfirm} /> }
    <CustomizedSnackbars isOpen={alertToggleData.isOpen} message={alertToggleData.message} toggle={alertToggleData.toggle} />

      <Container>
      <Box pt={14}>
          </Box>
        <Stack direction="row" alignItems="center" justifyContent="space-between" mb={5}>
          <Typography variant="h4" gutterBottom>
            Contact List
          </Typography>
          <Button
            variant="contained"
            component={RouterLink}
            to="addcontact"
            startIcon={<Icon icon={plusFill} />}
          >
            New Contact
          </Button>
        </Stack>

        <Card>
        <UserListToolbar
            numSelected={selected.length}
            filterName={filterName}
            onFilterName={handleFilterByName}
            deletaManyHandel={deletaManyHandel}
            trash={true}
          />
         
            <TableContainer sx={{ minWidth: 800 }}>
              <Table>
                <UserListHead
                  order={order}
                  orderBy={orderBy}
                  headLabel={TABLE_HEAD}
                  rowCount={filteredUsers.length}
                  numSelected={selected.length}
                  onRequestSort={handleRequestSort}
                  onSelectAllClick={handleSelectAllClick}
                />
                <TableBody>
                  {filteredUsers
                    .map((row) => {
                      const { _id, name, email, message, subject } = row;
                      const isItemSelected = selected.indexOf( _id ) !== -1;

                      return (
                        <TableRow
                          hover
                          key={_id}
                          tabIndex={-1}
                          role="checkbox"
                          selected={isItemSelected}
                          aria-checked={isItemSelected}
                        >
                          <TableCell padding="checkbox">
                            <Checkbox
                              checked={isItemSelected}
                              onChange={(event) => handleClick(event, _id)}
                            />
                          </TableCell>
                          <TableCell component="th" scope="row" padding="none">
                            <Stack direction="row" alignItems="center" spacing={2}>
                              <Typography variant="subtitle2" noWrap>
                                {name}
                              </Typography>
                            </Stack>
                          </TableCell>
                          <TableCell align="left">{email}</TableCell>
                          <TableCell align="left">{message}</TableCell>
                          <TableCell align="left">{subject}</TableCell>


                          <TableCell align="right">
                            <UserMoreMenu actionHandler={actionHandler} id={_id} trash={false}/>
                          </TableCell>
                        </TableRow>
                      );
                    })}
                  {emptyRows > 0 && (
                    <TableRow style={{ height: 53 * emptyRows }}>
                      <TableCell colSpan={6} />
                    </TableRow>
                  )}
                </TableBody>
                {isUserNotFound && (
                  <TableBody>
                    <TableRow>
                      <TableCell align="center" colSpan={6} sx={{ py: 3 }}>
                        <SearchNotFound searchQuery={filterName} />
                      </TableCell>
                    </TableRow>
                  </TableBody>
                )}
              </Table>
            </TableContainer>
          

          <TablePagination
            rowsPerPageOptions={[5, 10, 25]}
            component="div"
            count={+rowsTotal}
            rowsPerPage={rowsPerPage}
            page={page}
            onPageChange={handleChangePage}
            onRowsPerPageChange={handleChangeRowsPerPage}
          />
        </Card>
      </Container>
    </Page>
  );

}
