import { useFormik, Form, FormikProvider } from 'formik';
import CustomizedSnackbars, { useAlertData } from './component/alert/CustomizedSnackbars'


// material

// ----------------------------------------------------------------------

const RootStyle = styled(Page)(({ theme }) => ({
  [theme.breakpoints.up('md')]: {
    display: 'flex'
  }
}));

const SectionStyle = styled(Card)(({ theme }) => ({
  width: '100%',
  maxWidth: 464,
  display: 'flex',
  flexDirection: 'column',
  justifyContent: 'center',
  margin: theme.spacing(2, 0, 2, 2)
}));

const ContentStyle = styled('div')(({ theme }) => ({
  minWidth: '100%',
  margin: 'auto ',
  display: 'flex',
 
  flexDirection: 'column',
  justifyContent: 'center',
  padding: theme.spacing(4, 0)
}));

function AddContactForm({ initValue }) {
  let { id } = useParams();
  let history = useHistory();
  const [ alertToggleData, setAlertToggelData ] = useState({
    isOpen: false,
    message: '',
    type: 'success',
    toggle: false
  });

  const insertApi = val => {
      console.log(val)
    fetch(`${HOST}contactus/add`, {
      method: 'POST',
      body:JSON.stringify(val),
      headers: {
        'Content-Type': 'application/json',
     },
    })
      .then(respose => respose.json())
      .then(doc => {
        console.log(doc)
        handleReset();
      setAlertToggelData((pre)=>({...pre, message:doc.message, isOpen:true, toggle: !alertToggleData.toggle}));

      })
      .catch(err => console.log(err))
  }

  const editApi = val => {
    console.log(val)
  fetch(`${HOST}contactus/upd`, {
    method: 'PUT',
    body:JSON.stringify(val),
    headers: {
      'Content-Type': 'application/json',
   },
  })
    .then(respose => respose.json())
    .then(doc => {
      console.log(doc)
      handleReset();
      history.push('/admin/contact');
    })
    .catch(err => console.log(err))
  }
  
  const RegisterSchema = Yup.object().shape({
    name: Yup.string()
      .min(2, 'Too Short!')
      .max(50, 'Too Long!')
      .required('First name required'),
    message: Yup.string().min(2,'Too Short'),
    email: Yup.string().email('Email must be a valid email address').required('Email is required'),
    subject: Yup.string(),
  });


  let formik = useFormik({
  
    initialValues: {
      name: initValue.name || '',
      email: initValue.email || '',
      message: initValue.message || '',
      subject: initValue.subject || '', 
    },
    validationSchema: RegisterSchema,
    onSubmit: (value) => {
      const val = value;
      if(id !== undefined){
        editApi({
            // title: val.category_title === '' ? initValue.categoryTitle : val.category_title,
            name: val.name,
            email: val.email,
            subject: val.subject,
            message: val.message,
            userId: id
         })
      }else{
      insertApi({
          name: val.name,
          email: val.email,
          message: val.message,
          subject: val.subject,
      })
      }
    }
  });



 

const { errors, touched, handleSubmit, isSubmitting, getFieldProps, handleReset } = formik;

return (
  <FormikProvider value={formik}>
    <CustomizedSnackbars isOpen={alertToggleData.isOpen} message={alertToggleData.message} toggle={alertToggleData.toggle} />

    <Form autoComplete="off" noValidate onSubmit={handleSubmit}>
      <Stack spacing={3}>
        {/* <Stack direction={{ xs: 'column', sm: 'row' }} spacing={2}> */}
        <TextField
          fullWidth
          label={"Name"}
          {...getFieldProps('name')}
          error={Boolean(touched.name && errors.name)}
          helperText={touched.name && errors.name}
          placeHolder={'Name'}
        />

        <TextField
          fullWidth
          multiline
          rows={4}
          label={"Message" }
          {...getFieldProps('message')}
          error={Boolean(touched.message && errors.message)}
          helperText={touched.message && errors.message}
          placeHolder='Message'
        />
        <TextField
          fullWidth
          label={"Email"}
          {...getFieldProps('email')}
          error={Boolean(touched.email && errors.email)}
          helperText={touched.email && errors.email}
          placeHolder={'Email'}
        />
        <TextField
          fullWidth
          label={"Subject"}
          {...getFieldProps('subject')}
          error={Boolean(touched.subject && errors.subject)}
          helperText={touched.subject && errors.name}
          placeHolder={'Subject'}
        />


        <LoadingButton
          fullWidth
          size="large"
          type="submit"
          variant="contained"
          loading={isSubmitting}
        >
          {id ? "Update" : "Submit"}
        </LoadingButton>
      </Stack>
    </Form>
  </FormikProvider>
);
}

export default function AddContact() {
  let { id } = useParams();
  const [change, setChange] = useState(false);
  const [initValue, setInitValue] = useState({
  });
  if(id !== undefined){
    useEffect(() => {
      fetch(`${HOST}contactus/find/${id}`)
      .then(res=>res.json())
      .then(res=>{
          if(res){
        setInitValue(res);
        console.log(res)
        }
      }).then(
        setTimeout(()=>setChange(true),100)
        // setChange(true)
      )
      .catch(err=>console.log(err))

    }, [id])
  }else{
    setTimeout(()=>setChange(true),0);
  }
  return (
    <Page title="User | Minimal-UI">
      <Container>
        <Box pt={12}></Box>
        <Stack direction="row" alignItems="center" justifyContent="space-between" mb={5}>
          <Typography variant="h4" gutterBottom>
            {id ? "Edit Contact" : "Add Contact"}
          </Typography>
        </Stack>
        <Card >
            <RootStyle>
              <ContentStyle>
                <Container>
                {change && <AddContactForm  initValue={initValue} /> }
                </Container>
              </ContentStyle>
            </RootStyle>
        </Card>
      </Container>
    </Page>
  );
}
