import React, { useState } from 'react'
const Joi = require('joi');
import { Container, Row, Col, Form, Button, Alert } from 'react-bootstrap'

import { SERVER } from '../../../../config'

const ContactForm = () => {
    const [fromData, setFromData] = useState({});

    const [alert, setAlert] = useState(false);
    const [isEmail, setIsEmail] = useState(false);
    const [emailErr, setEmailErr] = useState(true);

    const [validated, setValidated] = useState(false);

    const sendForm = (val) => {
        console.log(val);
        fetch(`${SERVER}contactus/add`, {
            method: 'POST',
            body: JSON.stringify(val),
            headers: {
                'Content-Type': 'application/json',
            }
        }).then(res => res.json()).then(doc => {
            console.log(doc);
            setAlert(true);
            setFromData(() => {
                return {
                    message: '',
                    email: '',
                    name: '',
                    subject: ''
                }
            })
            

        }).catch(err => console.log(err));
    }
    const onclickHandeler = (event) => {
        const nam = event.target.name;
        const val = event.target.value;

        // let re = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;

        // if (nam === 'email' && re.test(val)) {
        //     if(nam != ''){
        //         setEmailErr(true);
        //     }
        //     setIsEmail(true);
        //     console.log(nam)
        //     // this is a valid email address
        //     // call setState({email: email}) to update the email
        //     // or update the data in redux store.
        // }
        // else {
        //     console.log('this is not wour');
        //     // invalid email, maybe show an error to the user.
        // }

        setFromData((pre) => {
            return {
                ...pre,
                [nam]: val
            }
        })

    }

    const handleSubmit = (event) => {
        event.preventDefault();
        const form = event.currentTarget;

        if (form.checkValidity() === false) {
            event.preventDefault();
            event.stopPropagation();
        }else{
             sendForm(fromData); 
             return setValidated(false);   
        }
        
        setValidated(true);
    };

    return (

        <Form noValidate validated={validated} onSubmit={handleSubmit}>
            {alert && <Alert variant='success'>
                Your Message Has Been Sent!
            </Alert>}
            <Row>


                <Col md={12}>
                    <Form.Group className="mb-3" controlId="exampleForm.ControlTextarea1">
                        <Form.Control as="textarea" rows={10} placeholder="Enter Your Comment" name='message' value={fromData.message} onChange={onclickHandeler} required />
                        <Form.Control.Feedback>Looks good!</Form.Control.Feedback>
                        <Form.Control.Feedback type="invalid">
                            Please Fill Out This Field.
                        </Form.Control.Feedback>
                    </Form.Group>
                </Col>
                <Col sm={6}>
                    <Form.Group className="mb-3" controlId="exampleForm.ControlInput1">
                        <Form.Control size="lg" type="text" placeholder="Enter Your Name" name='name' value={fromData.name} onChange={onclickHandeler} required />
                        <Form.Control.Feedback>Looks good!</Form.Control.Feedback>
                        <Form.Control.Feedback type="invalid">
                            Please Fill Out This Field.
                        </Form.Control.Feedback>
                    </Form.Group>

                </Col>
                <Col sm={6}>
                    <Form.Group className="mb-3" controlId="exampleForm.ControlInput1">
                        <Form.Control size="lg" type="email" placeholder="enter your email" name='email' value={fromData.email} onChange={onclickHandeler} required />
                        <Form.Control.Feedback>Looks good!</Form.Control.Feedback>
                        <Form.Control.Feedback type="invalid">
                            { emailErr ? 'Please Fill Valid Email' : 'Please Fill Out This Field.'}
                        </Form.Control.Feedback>
                    </Form.Group>
                </Col>
                <Col md={12}>
                    <Form.Group className="mb-3" controlId="exampleForm.ControlInput1">
                        <Form.Control size="lg" type="text" placeholder="Enter Subject" name='subject' value={fromData.subject} onChange={onclickHandeler} required />
                        <Form.Control.Feedback>Looks good!</Form.Control.Feedback>
                        <Form.Control.Feedback type="invalid">
                            Please Fill Out This Field.
                        </Form.Control.Feedback>
                    </Form.Group>

                </Col>
            </Row>
            <Form.Group className="mt-3" controlId="exampleForm.ControlInput1">
                <Button variant="danger" type='submit'>SEND MESSAGE</Button>
            </Form.Group>
        </Form>

    )
}

export default ContactForm
