import { useEffect, useState } from 'react';
import { routers, RouteWithSubRoutes } from '../../routes'
import { useLocation,Switch,Route } from 'react-router-dom';
import { matchRoutes, renderRoutes } from "react-router-config";

// material
import { styled } from '@material-ui/core/styles';
//
import DashboardNavbar from './DashboardNavbar';
import DashboardSidebar from './DashboardSidebar';
import HOST from '../../components/Env';

// ----------------------------------------------------------------------

const APP_BAR_MOBILE = 64;
const APP_BAR_DESKTOP = 92;

const RootStyle = styled('div')({
  display: 'flex',
  minWidth: '100%',
  minHeight: '100%',
  overflow: 'hidden'
});

const MainStyle = styled('div')(({ theme }) => ({
  flexGrow: 1,
  overflow: 'auto',
  minHeight: '100%',
  paddingTop: APP_BAR_MOBILE + 24,
  paddingBottom: theme.spacing(10),
  [theme.breakpoints.up('lg')]: {
    paddingTop: APP_BAR_DESKTOP + 24,
    paddingLeft: theme.spacing(2),
    paddingRight: theme.spacing(2)
  }
}));

// ----------------------------------------------------------------------

export default function DashboardLayout(props) {
  const [open, setOpen] = useState(false);
  const [userInfo, setUserInfo] = useState({});

  useEffect(()=>{
    fetch(`${HOST}user/find`,{
      method: 'GET',
      headers:{
        'Authorization': `bearer ${localStorage.getItem('token')}`
      }
    }).then(res=>res.json())
    .then(res=>{
      setUserInfo(res);
      console.log(res);
    }).catch(err=>console.log(err))

  },[])
  return (
    <RootStyle>
      <DashboardNavbar userInfo={userInfo} onOpenSidebar={() => setOpen(true)} />
      <DashboardSidebar userInfo={userInfo} isOpenSidebar={open} onCloseSidebar={() => setOpen(false)} />
        {props.children}
    </RootStyle>
  );
}
