import * as React from 'react';
import { useTheme } from '@mui/material/styles';
import OutlinedInput from '@mui/material/OutlinedInput';
import InputLabel from '@mui/material/InputLabel';
import MenuItem from '@mui/material/MenuItem';
import FormControl from '@mui/material/FormControl';
import Select from '@mui/material/Select';
import Chip from '@material-ui/core/Chip';
import HOST from '../../../components/Env'

// import { catList } from '../useApi'

const ITEM_HEIGHT = 48;
const ITEM_PADDING_TOP = 8;
const MenuProps = {
  PaperProps: {
    style: {
      maxHeight: ITEM_HEIGHT * 4.5 + ITEM_PADDING_TOP,
      width: 250,
    },
  },
};

const names = [
  'Oliver Hansen',
  'Van Henry',
  'April Tucker',
  'Ralph Hubbard',
  'Omar Alexander',
  'Carlos Abbott',
  'Miriam Wagner',
  'Bradley Wilkerson',
  'Virginia Andrews',
  'Kelly Snyder',
];

function getStyles(name, personName, theme) {
  return {
    fontWeight:
      personName.indexOf(name) === -1
        ? theme.typography.fontWeightRegular
        : theme.typography.fontWeightMedium,
  };
}

export default function MultipleSelect({ multiSelectHandler, initdata }) {
  const initialData = initdata;
  const theme = useTheme();
  const [catName, setCatName] = React.useState([]);
  const [categoryList, setCategoryList] = React.useState([]);
  const handleChange = (event) => {
    const {
      target: { value },
    } = event;
    setCatName(
      // On autofill we get a the stringified value.
    value
    );
    multiSelectHandler(value)
  };
  console.log(catName);
  const chipClickHandler = (event) => {
    
  }

  React.useEffect(() => {
      fetch(`${HOST}category/`, {
        method: 'GET'
      }).then(respose=>respose.json())
      .then(doc=>{
        doc.map((ele)=>setCategoryList((pre)=>[...pre, ele]))
      })
      .catch(err=>console.log(err))
      return ( () => setCategoryList(()=>[])) 
  }, [])

  React.useEffect(() => {
    if(initialData){
      initialData.map(val=>setCatName((pre)=>[...pre, val]));
    }
    multiSelectHandler(catName)
  },[initialData])
  
  return (
    <div>
      <FormControl sx={{ m: 1, width: 1 }}>
        <InputLabel id="demo-multiple-name-label">Select Category</InputLabel>
        <Select
          labelId="demo-multiple-name-label"
          id="demo-multiple-name"
          multiple
          value={catName}
          onChange={handleChange}
          input={<OutlinedInput label="Name" />}
          renderValue={(selected) => (
            <div>
              {selected.map((value,i) => (
                <Chip key={i} label={value}/>
              ))}
            </div>
          )}
          MenuProps={MenuProps}
        >
          {categoryList.map((name) => (
            <MenuItem
              key={name._id}
              value={name.categoryTitle}
              style={getStyles(name, catName, theme)}
            >
              { name.categoryTitle }
            </MenuItem>
          ))}
        </Select>
      </FormControl>
    </div>
  );
}