import * as React from 'react';
import Stack from '@mui/material/Stack';
import Button from '@mui/material/Button';
import Snackbar from '@mui/material/Snackbar';
import MuiAlert from '@mui/material/Alert';


export function useAlertData(message='',type='success'){
  const [alertType, setAlertType] = React.useState('success');
  const [alertMessage, setAlertMessage] = React.useState('seccess full message');
  React.useEffect(()=>{
    setAlertType(type);
    setAlertMessage(message);
  },[message,type]);

  return {
    alertType,
    alertMessage
  } 
}


const Alert = React.forwardRef(function Alert(props, ref) {
  return <MuiAlert elevation={6} ref={ref} variant="filled" {...props} />;
});


export default function CustomizedSnackbars({ toggle, isOpen=false, message='this is a test message' , type= 'success' , time = 5000}) {
  const [open, setOpen] = React.useState(true);
  const [mess, setMess] = React.useState('');
  const [ty, setTy] = React.useState('success');
  // const [toggle, setToggle] = React.useState();

  const handleClick = () => {
    setOpen(true);
  };
  React.useEffect(()=>{
    setOpen(isOpen);
    setMess(message);
    setTy(type);
    setTimeout(() => {
      setOpen(false)
    }, time);
  },[isOpen, message,type, toggle]);

  const handleClose = (event, reason) => {
    if (reason === 'clickaway') {
      return;
    }
    setOpen(false);
  };

  return (
    <Stack spacing={2} sx={{ width: '100%' }}>
      <Snackbar open={open} autoHideDuration={time} onClose={handleClose} anchorOrigin={{vertical: 'top',
          horizontal: 'right'}}>
        <Alert onClose={handleClose} severity={ty} sx={{ width: '100%' }}>
          {mess}
        </Alert>
      </Snackbar>
    </Stack>
  );
}


