const RootStyle = styled(Page)(({ theme }) => ({
  [theme.breakpoints.up('md')]: {
    display: 'flex'
  }
}));

const SectionStyle = styled(Card)(({ theme }) => ({
  width: '100%',
  maxWidth: 464,
  display: 'flex',
  flexDirection: 'column',
  justifyContent: 'center',
  margin: theme.spacing(2, 0, 2, 2)
}));

const ContentStyle = styled('div')(({ theme }) => ({
  minWidth: '100%',
  margin: 'auto ',
  display: 'flex',
  minHeight: '100vh',
  flexDirection: 'column',
  justifyContent: 'center',
  padding: theme.spacing(4, 0)
}));
const FormStyle = styled('form')(({ theme }) => ({
  minWidth: '100%',
  margin: 'auto ',
  display: 'flex',
  minHeight: '100vh',
  flexDirection: 'column',
  justifyContent: 'center',
  padding: theme.spacing(12, 0)
}));

function AddPostForm({initValue}) {
  let history = useHistory();
  let { id } = useParams();
  const [image, setImage] = useState([]);
  const [category, setCategory] = useState([]);
  const [ alertToggleData, setAlertToggelData ] = useState({
    isOpen: false,
    message: '',
    type: 'success',
    toggle: false
  });
 
  console.log('cat '+ category);


  const editApi = val => {
    fetch(`${HOST}posts/update`, {
      method: 'PUT',
      body: val
    })
      .then(respose => respose.json())
      .then(doc => {
        console.log(doc)
        handleReset();
        history.push('/admin/post');
      })
      .catch(err => console.log(err))
  } 
  const insertApi = val => {
    fetch(`${HOST}posts/add`, {
      method: 'POST',
      body: val
    })
      .then(respose => respose.json())
      .then(doc => {
        console.log(doc)
        handleReset();
      setAlertToggelData((pre)=>({...pre, message:doc.message, isOpen:true, toggle: !alertToggleData.toggle}));

      })
      .catch(err => console.log(err))
  }

  const RegisterSchema = Yup.object().shape({
    title: Yup.string()
      .min(2, 'Too Short!')
      .max(80, 'Too Long!')
      .required('Title required'),
    paragraph: Yup.string().min(2, 'Too Short!').required('Paragraph Required'),
    seo_title: Yup.string().min(3,"Too Short"),
    seo_description: Yup.string().min(3,"Too Short"),
    seo_keyword: Yup.string().min(3,"Too Short"),
    slug: Yup.string().min(3,"Too Short"),
  });
  let formik = useFormik({
    initialValues: {
      title: initValue.title || '',
      paragraph: initValue.paragraph || '',
      seo_title: initValue.seo_title || '',
      seo_keyword: initValue.seo_keyword || '',
      seo_description: initValue.seo_description || '',
      slug: initValue.slug || '',
    },
    validationSchema: RegisterSchema,
    onSubmit: (value) => {
      const val = value;
      const dataForm = new FormData();
      image.forEach(element => {
        dataForm.append('image', element);
      });
      if(category.length === 0){
        const catVal = initValue.category;
        const catArr = catVal.toString().split(',');
        catArr.forEach(element => {
          dataForm.append('category', element);
        });
      }else{
        category.forEach(element => {
          dataForm.append('category', element);
      });
      }
        
      if(id !== undefined){
        const keysEdit = Object.keys(initValue);
        keysEdit.forEach((key) => {
          
          if(key === 'category'){
          return
          }
          const keyValue = val[key]; 

        dataForm.append(key,keyValue);
      });
      dataForm.append('userId',initValue._id)
        editApi(dataForm);
      }else{
        const keys = Object.keys(val);
        keys.forEach((key) => {
          dataForm.append(key,val[key]);
        });
       insertApi(dataForm);
      }
    }
  });

const { errors, touched, handleSubmit, isSubmitting, getFieldProps, handleReset } = formik;

const multiSelectHandler = (data) => {
  setCategory(data)
  console.log('this is ' + data);
}
const dropzoneHandler = (status, meta, file) => {
  if (meta.status === 'done') {
    if (image.indexOf(file) === -1) {
      setImage((image) => [...image, file])
    }
  } else if (meta.status === 'removed') {
    const fileter = image.filter(img => img !== file)
    setImage(fileter)
  }

}

return (
  <>
   <CustomizedSnackbars isOpen={alertToggleData.isOpen} message={alertToggleData.message} toggle={alertToggleData.toggle} />
  <FormikProvider value={formik}>
    <Form autoComplete="off" noValidate onSubmit={handleSubmit}>
      <Stack spacing={3}>
        {/* <Stack direction={{ xs: 'column', sm: 'row' }} spacing={2}> */}
        <TextField
          fullWidth
          label={'Title'}
          {...getFieldProps('title')}
          error={Boolean(touched.title && errors.title)}
          helperText={touched.title && errors.title}
          placeholder='Title'

        />
        <TextField
          fullWidth
          multiline
          rows={4}
          label={'Paragraph'}
          {...getFieldProps('paragraph')}
          error={Boolean(touched.paragraph && errors.paragraph)}
          helperText={touched.paragraph && errors.paragraph}
          placeholder='Paragraph'
        />
        <MultipleSelect multiSelectHandler={multiSelectHandler} initdata={initValue.category} />
        <MyUploader dropzoneHandler={dropzoneHandler}  />

        <TextField
          fullWidth
          label={'SEO Title'}
          {...getFieldProps('seo_title')}
          error={Boolean(touched.seo_title && errors.seo_title)}
          helperText={touched.seo_title && errors.seo_title}
          placeholder='SEO Title'
        />
        <TextField
          fullWidth
          label={'SEO Description'}
          {...getFieldProps('seo_description')}
          error={Boolean(touched.seo_description && errors.seo_description)}
          helperText={touched.seo_description && errors.seo_description}
          placeholder='SEO Description'

        />
        {id && <TextField
        fullWidth
        label={'SEO Slug'}
        {...getFieldProps('slug')}
        error={Boolean(touched.slug && errors.slug)}
        helperText={touched.slug && errors.slug}
        placeholder='SEO Slug'

      /> }
        <TextField
          fullWidth
          label={'SEO Keyword'}
          {...getFieldProps('seo_keyword')}
          error={Boolean(touched.seo_keyword && errors.seo_keyword)}
          helperText={touched.seo_keyword && errors.seo_keyword}
          placeholder='SEO Keyword'
        />


        <LoadingButton
          fullWidth
          size="large"
          type="submit"
          variant="contained"
          loading={isSubmitting}
        >
         { id ? 'Update' : "Submit" }
        </LoadingButton>
      </Stack>
    </Form>
  </FormikProvider>
  </>
);
}






export default function AddUser() {
  let { id } = useParams();
  const [change, setChange] = useState(false);
  const [initValue, setInitValue] = useState({
  });

  if(id !== undefined){
    useEffect(() => {
      
      fetch(`${HOST}posts/find/${id}`)
      .then(res=>res.json())
      .then(res=>{
          if(res){
        setInitValue(res);
        console.log(res);
        }
      }).then(
        setTimeout(()=>setChange(true),100)
        // setChange(true)
      )
      .catch(err=>console.log(err))
      
    }, [id])
  }else{
    setTimeout(()=>setChange(true),0);
  }


  return (
    <Page title="User | Minimal-UI">
      <Container>
        <Box pt={12}></Box>
        
        <Stack direction="row" alignItems="center" justifyContent="space-between" mb={5}>
          <Typography variant="h4" gutterBottom>
            {id ? 'Edit Post' : 'Add Post' }  
          </Typography>
        </Stack>
        <Card >
        
            <RootStyle>
              <ContentStyle>
                <Container>
                {change && <AddPostForm initValue={initValue} /> }
                </Container>
              </ContentStyle>
            </RootStyle>
  
        </Card>




      </Container>
    </Page>
  );
}
