import React, {useState, useEffect } from 'react'


import { Container, Row, Col, Button,Pagination, PageItem } from 'react-bootstrap'
import Moment from 'react-moment';
import {
    BrowserRouter as Router,
    Link,
    Switch,
    Route,
    useParams,
    useHistory,
    useLocation,
  } from "react-router-dom";
import './BlogMain.css'
import { HiOutlineSearch } from 'react-icons/hi'
import { FaAngleLeft, FaAngleRight, FaRegComments, FaRegUser } from 'react-icons/fa'
import BlogSideBar from '../blogSideBar/BlogSideBar'
import { SERVER } from '../../../config'
import { indexOf } from 'lodash';
import { lastIndexOf } from 'lodash';

const BlogMain = () => {
    let param = useParams();
    let { catId, pathname } = useLocation();
    let history = useHistory();
  const [catIdName, setCatIdName] = useState(catId)
  const [currentCat, setCurrentCat] = useState('')
  const [postList, setPostList ] = useState([])
  const [pegItem, setPegItem] = useState([])
  const [pagData, setPagData] = useState({
    diff:10, active: param.pageId ?? 1,
  })
  
  let { active } = pagData;


  if(catId != catIdName){
    setCatIdName(catId);
    setPagData((pre)=>{
     return{ ...pre,active:1}
    })
  }

 
  
const checkPath = (i) => {
  if(/\d/.test(pathname)){
      return (`${pathname.replace(/\d+/g,'')}`);
    }else{
      return (`${pathname}page/`)
    }
 } 
const pegHandler =(act)=>{
    if(act == undefined) return
    setPagData((pre)=>{
        return{
            ...pre,
            active:act,
        }
    })
    history.push(`${checkPath()}${act}`);
}

useEffect(() => {

  if(catId === undefined && pathname.includes('/category/page/')){
      const ext  = pathname.split('/')[3];
      setCurrentCat(ext);
  }else{
    setCurrentCat(catId);
  }
  fetch(`${SERVER}posts/filter?page=${pagData.active - 1}&row=${pagData.diff}&catId=${currentCat || null }`, {
    headers: {
      'Content-Type': 'application/json',
   },
  method: 'GET',

    }).then(respose=>respose.json())
    .then(doc=>{
      (doc.getPost).map((ele)=>setPostList((pre)=>[...pre, ele]))
        for(let i = 1; i <= Math.ceil(doc.total / pagData.diff); i++ ){
          setPegItem((pre)=>{
            return [...pre , i]
          }
        )
      }
      console.log(doc)
    }
    )
    .catch(err=>console.log(err))
    
    return () => {
      setPostList(()=>[])
      setPegItem(()=>[])
    } 
    
}, [catId, active, currentCat])
console.log(postList)



const preNex = (ele) => {

    if(ele == 'pre'){
      if(pagData.active <= 1 ){
        return
      }else{
        history.push(`${checkPath(pagData.active)+(parseInt(pagData.active) - 1)}`) 
    
      return(
        setPagData((pre)=>{
          return{ ...pre ,active: parseInt(pagData.active) - 1} 
        })
      )
    }
  }else{
    if(pagData.active >= pegItem.length ){
      return
    }else{
      history.push({
        pathname: `${checkPath(pagData.active)}${(parseInt(pagData.active) + 1)}`,
        state: { name: 'Hello'}
      }) 
      return(
        setPagData((pre)=>{
          return{ ...pre ,active: parseInt(pagData.active) + 1} 
        })
      )
    }
  }
}


console.log(catIdName)
    return (
        <section className='blog_area area-padding'>
            <Container>
                <Row>
                    <Col lg={8} className='mb-5 me-lg-0'>

                        <div className="blog_left_sidebar">

                            {postList.map((ele)=>{
                                const par = ele.paragraph;
                                const para= (par).substr(((par).indexOf('>') + 1),((par).indexOf('</p>') - 6));
                                return(
                            <article key={ele._id} className="blog_item">
                                <div className="blog_item_img">
                                    <img className="card-img rounded-0" src={`${SERVER}images/post/${ele.image[0]}`} alt="" />
                                    <Link to={{pathname:`/blogs/blog/${ele.slug}/`,blogId:`${ele._id}`}} className="blog_item_date">
                                        <h3><Moment format="DD">
                                        {ele.modified}
                                          </Moment></h3>
                                        <p><Moment format="MMM">
                                        {ele.modified}
                                          </Moment></p>
                                    </Link>
                                </div>
                                <div className="blog_details">
                                    <Link to={{pathname:`/blogs/blog/${ele.slug}/`,blogId:`${ele._id}`}} className="d-inline-block" >
                                    <h2>{ele.title }</h2>
                                    </Link>
                                    {/* <h3>{para}</h3> */}
                                    <p>{para.length >= 175 ? `${para.substring(0,175)} ...` : para}</p>
                                    <ul className="blog-info-link">
                                        <li><Link to="#"><i className="far fa-user"><FaRegUser /></i>{(ele.category).join(', ')}</Link></li>
                                        <li><Link to="#"><i className="far fa-comments"><FaRegComments /></i> 03 Comments</Link></li>
                                    </ul>
                                </div>
                            </article>)

                            })}
                            <nav className="blog-pagination justify-content-center d-flex">
                            <Pagination size="lg">
                                <Pagination.Prev onClick={()=>preNex(`pre`)} />
                                  {pegItem.map((ele,i)=>{
                                      return (
                                          <Pagination.Item activeLabel key={i} active={i === pagData.active - 1} onClick={(current)=>pegHandler(current.target.text)} value={ele}>
                                            {ele}
                                          </Pagination.Item>
                                      )
                                  })}
                                  <Pagination.Next onClick={()=>preNex(`nex`)} />
                            </Pagination>
                               
                            </nav>
                        </div>
                    </Col>
                    <BlogSideBar />
                </Row>
            </Container>

        </section>
    )
}


export default BlogMain
