import React from 'react'
// material
import { Box, Grid, Container, Typography } from '@material-ui/core';
import { Link as ThisLink } from 'react-router-dom'
import { useEffect } from 'react';
// components
import Page from '../components/Page';
import {
  AppTasks,
  AppNewUsers,
  AppBugReports,
  AppItemOrders,
  AppNewsUpdate,
  AppWeeklySales,
  AppOrderTimeline,
  AppCurrentVisits,
  AppWebsiteVisits,
  AppTrafficBySite,
  AppCurrentSubject,
  AppConversionRates,
} from '../components/_dashboard/app';
import AppCustomCard from '../components/_dashboard/app/AppCustomCard'
import HOST from '../components/Env';
import CustomizedSnackbars from './component/alert/CustomizedSnackbars';

// ----------------------------------------------------------------------

export default function DashboardApp() {
  const [ data, setData ] = React.useState([])
  React.useEffect(()=>{
    fetch(`${HOST}user/dasboard`,{
      method:"GET",
      Headers:{
        'ContentType':'application/json'
      }}).then(res=>res.json())
      .then(res=>{
        setData(res);
        console.log(res);
      })
      .catch(err=>console.log(err));
    return ()=>setData('');
  },[]);


  return (
    <Page title="Dashboard | Minimal-UI">
      <Container maxWidth="xl">
        
      <Box pt={14}>
          </Box>
      <CustomizedSnackbars />

        <Box sx={{ pb: 5 }}>
          <Typography variant="h4">Hi, Welcome</Typography>
        </Box>
        <Grid container spacing={3}>
          <Grid component={ThisLink} item xs={12} sm={6} md={3}>
            <AppCustomCard name={'Users'} count={data.users} />
          </Grid>
          <Grid item xs={12} component={ThisLink} to='category' sm={6} md={3}>
            <AppNewUsers name={'Categories'} count={data.categories}/>
          </Grid>
          <Grid item xs={12} component={ThisLink} to='post' sm={6} md={3}>
            <AppItemOrders name={'Posts'} count={data.posts} />
          </Grid>
          <Grid item xs={12} component={ThisLink} to='contact'  sm={6} md={3}>
            <AppBugReports name={'Contact'} count={data.contact} />
          </Grid>

        </Grid>
      </Container>
    </Page>
  );
}
